<template>
    <div id="NewsInformation">
        <div class="banner container-fuild text-center">NEWS </div>
        <bread></bread>
        <div class="container wow slideInUp">
            <div class="container text-center">
                <h3></h3>
                <p style="color:#b2b2b2">Scientific News</p>
            </div>
            <div class="nav container text-center" >
                <span :class=" { active: !isActive }" id="1" @click="innerdata">Scientific research exchanges</span>
                <span :class=" { active: isActive }" id="2" @click="innerdata">Scientific research experiments</span>
            </div>
            <span class="glyphicon glyphicon-sort " id="reverse" aria-hidden="true" @click="dataReverse"></span>
            <ul class="news-container container-fuild">
                <li v-for="item in text" :key="item.id" class="wow fadeIn" @click="jumpTO(item)">
                    <div class="content">
                        <p>{{item.tittle}}</p>
                        <p>{{item.introduce}}</p>
                    </div>
                    <div class="time">
                        <p>{{item.data}}</p>
                        <span>{{item.year}}</span>
                    </div>
                    <div class="circle">
                        <img src="../assets/img/circle.png">
                        <i class="line center-block"></i>
                    </div>
                </li>
            </ul>
            <div class="contaianer-fuild text-center more">
                <i class="glyphicon glyphicon-th"></i>
            </div>
        </div>
    </div>
</template>
<script>
import { WOW } from 'wowjs';
import Bread from '../components/Bread.vue';
import Vue from 'vue';
import newsList from '../assets/data/news.json';
export default {
  components: { Bread },
    name: 'NewsInformation',
    data(){
        return{
            newsList,
            text:[],
            test: '#hello',
            isActive: false,
            list: [],
        }
    },
    computed:{
        
    },
    methods:{
        innerdata(){
        const a = event.target.id;
        this.list.push(a);
        if(this.list.length>1){
            let b = this.list[this.list.length-1];
            let c = this.list[this.list.length-2];
            if(b!=c){
                this.isActive = !this.isActive;
                return a == 1? this.text =this.newsList['Sheet1']:this.text = this.newsList['Sheet2'];
            }
        }else{
            if(this.list!=1){
                this.isActive = !this.isActive; 
                return a == 1? this.text =this.newsList['Sheet1']:this.text = this.newsList['Sheet2'];
            }
        }
        },
        jumpTO(e){
            //  let activeLink = document.querySelector('span.active');
            //     if(activeLink) {
            //         console.log(activeLink.id);
            //     } else {
            //         console.log("No 'a' elements with 'active' class found");
            //     }
            this.$router.push({path:'/newsinformation/details' + e.id,query:e});
        },
        dataReverse(){
            this.text.reverse()
        },
    },
    mounted(){
        this.text = this.newsList.Sheet1;
        var wow = new WOW();
        wow.init();
        
    },
}
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner_2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
.nav{
    margin: 20px 0;
}
.nav>span{
    display: inline-block;
    text-decoration: none;
    height: 45px;
    line-height: 45px;
    color: #333;
    border: 1px solid #333;
}
.nav>span.active{
    color: #1e73be;
    border-color: #1e73be;
}
.nav>span:hover{
    color: #ff1818;
    border-color:  #ff1b1b;
}
.news-container{
    overflow: hidden;
    margin-bottom: 0;
}
.news-container>li{
    width: 55.6%;
    height: 120px;
    float: left;
    color: #333;
    text-align: right;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
}
.news-container>li:hover{
    color: #1e73be;
    border: 1px solid #1e73be;
    cursor: pointer;
}
.news-container>li:nth-of-type(2n){
    float: right;
    text-align: left;
}
.news-container>li>.content{
    width: 60%;
    float: left;
    padding: 20px 0;
}
.news-container>li>.time{
    width: 20%;
    float: left;
    padding: 10px 0;
}
.news-container>li>.time>p{
    font-size: 30px;
    margin: 5px 0;
}
.news-container>li>.circle{
    width: 20%;
    height: 100%;
    float: left;
    position: relative;
}
.news-container>li>.circle>img{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 20px;
    height: 20px;
}
.news-container>li>.circle>i{
    display: block;
    width: 1px;
    height: 100%;
    background: #707070;
}
.news-container>li:nth-of-type(2n)>.content{
    float: right;
}
.news-container>li:nth-of-type(2n)>.time{
    float: right;
}
.news-container>li:nth-of-type(2n)>.circle{
    float: right;
}
.news-container>li:nth-of-type(1)>.circle>i{
    height: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
}
.more{
    font-size: 25px;
    color: #707070;
}
.more>i{
    cursor: pointer;
}
#reverse{
    margin: 0 auto;
    display: block;
    text-align: center;
}
#reverse:hover{
    color: #1e73be;
}
@media screen and (max-width: 767px){
    .news-container>li{
        width: 100%;
    }
    .news-container>li>.content{
        width: 70%;
        text-align: left;
        float: right;
    }
    .news-container>li>.time{
        width: 30%;
        text-align: left;
        float: right;
    }
    .news-container>li>.circle{
        display: none;
    }
}
</style>




